<template>
  <div class="popup-container popup-container-hongbao" effect="hongbao"
       :class="{'popup-showing active': state == 1, 'popup-showing popup-hidden': state == 2}">

    <img cheers
         class="cheers"
         :class="{'visible': step === 3 && state === 1}"
         src="//assets.qkcdn.com/images/760fda2edba3e07302fde315a7f85d4b.png" >

    <div class="popup von-popup popup-hongbao">
      <div class="popup-body" :class="{
        'step-2': step == 2,
        'step-3': step == 3
      }">

        <div class="text-2">
          请选择
        </div>

        <div class="text-3">
          <div class="letter-spacing">&nbsp;&nbsp;恭喜！</div>
          <div>现金{{ parseFloat(reward) }}元已入账</div>
          <div class="remind">满10元可提现至微信、支付宝</div>
        </div>

        <div class="hongbao-buttons">
          <input class="input-master-id" v-model="masterId" type="tel" placeholder="输入邀请ID" />

          <button class="button button-light btn-with-uid" :class="{'show-form': showForm}" @click="getHongbaoByUid()">
            {{ btnWithUidText }}
          </button>

          <button class="button button-light btn-without-uid" @click="fetchReward(0)">
            无邀请ID
          </button>
        </div>

        <button class="button button-light btn-continue" @click="makeMoney()">
          <div class="continue-text">开始赚钱</div>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
  /* eslint eqeqeq: "off", camelcase: "off", no-unused-vars: "off" */

  import channel from './channel'

  const popup_leave_duration = 300

  function preventDefault (e) {
    e.preventDefault()
  }

  export default {
    props: {
      gift: {
        type: Number,
        default: 1
      },

      makeMoneyCallback: Function
    },

    data () {
      return {
        state: 0, // 0: hidden, 1: showing, 2: active
        step: 2,
        reward: 1.0,
        showForm: false,
        btnWithUidText: '有邀请ID',
        masterId: ''
      }
    },

    methods: {
      show () {
        this.state = 1
        document.body.classList.add('popup-open')
        $backdrop.show()
        document.addEventListener('touchmove', preventDefault)

        if (this.gift == 0) return  // 已领过红包

        if (this.gift == 3) {
          // 合作渠道用户, 直接发红包
          return this.fetchReward(0)
        }

        if (this.gift == 1 && this.masterId) {
          return this.fetchReward(this.masterId)
        }

        return new Promise((resolve, reject) => {
          this.$on('PopupButtonClickEvent', (data) => {
            resolve(data.buttonIndex)
          })
        })
      },

      hide () {
        this.state = 2

        $backdrop.hide()
        setTimeout(() => {
          document.body.classList.remove('popup-open')
        }, 300)

        document.removeEventListener('touchmove', preventDefault)

        setTimeout(() => {
          this.state = 0
        }, popup_leave_duration)
      },

      fetchReward (master_id) {
        $api.giftSend(master_id)
          .then(res => res.data && res.data.payload)
          .then((payload) => {
            // 显示奖励弹层, 发消息通知 dashboard 组件, 更新状态
            this.reward = payload.reward
            channel.$emit('updateIncome', payload)
            this.step = 3

            this.cheers()
          })
      },

      makeMoney () {
        this.hide()
        setTimeout(() => {
          if (this.makeMoneyCallback) this.makeMoneyCallback()
        }, 300)
      },

      getHongbaoByUid () {
        if (this.showForm) {
          let re_master_id = /^[\d]{8,11}$/

          if (!this.masterId) {
            $toast.show('请输入邀请 ID')
            return
          }

          if (!re_master_id.test(this.masterId)) {
            $toast.show('请输入有效邀请人 ID')
            return
          }

          return this.fetchReward(this.masterId)
        }

        this.showForm = true
        this.btnWithUidText = '提交'
      },

      cheers () {
        const el = document.querySelector('[cheers]')
        el.style.top = (document.querySelector('.popup-hongbao').offsetTop - 80) + 'px'
      }
    }
  }
</script>
<style lang="scss">
  @import "../../scss/variables";
  @import "../../scss/mixins";

  .popup-container-hongbao {
    .cheers {
      position: absolute !important;
      left: 0;
      top: 0;
      visibility: hidden;
      opacity: 0;
      width: 375px;
      height: auto;
      transition: opacity 0.15s ease 0.3s;
      -webkit-transition: opacity 0.15s ease 0.3s;

      &.visible {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  .popup.von-popup.popup-hongbao {
    background-color: transparent !important;
  }

  .popup-hongbao {
    .popup-body {
      width: 275px;
      height: 408px;

      @include background-image(url(//assets.qkcdn.com/images/303da6d86e06b67ccba6fac1177c0be3.png), 275px 408px);

      background-color: transparent;
      padding: 59px 30px 30px 30px !important;
      color: #FFF;
      position: relative;
      overflow: hidden;

      .button-close {
        position: absolute;
        top: 5px;
        right: 5px;
        font-size: 30px;
      }

      .btn-continue {
        margin: 0;
        position: absolute;
        left: 30px;
        bottom: 50px;
        color: #3a3a3a;
        background-color: #f7cc86 !important;
        width: 215px;
        height: 44px;
        font-size: 14px;
        border-radius: 2px;

        @include font-regular();

        &:active,
        &.active,
        &.activated {
          background: #f7cc86;
          box-shadow: none;
          opacity: 1;
        }
      }

      .btn-open-hongbao {
        z-index: 1;
      }

      .btn-continue {
        z-index: 5;
      }

      .hongbao-buttons {
        z-index: 2;
        position: absolute;
        left: 0;
        bottom: 50px;
        height: 103px;
        width: 100%;
        padding: 0 30px;

        .input-master-id {
          width: 130px;
          height: 44px;
          background: transparent;
          border-bottom: 1px solid rgba(#fed7b0, 0.5);
          color: #fed7b0;
          margin: 0 0 0 2px;
          padding: 12px 0;
          font-size: 20px;
          line-height: 20px;
          text-align: left;
          border-radius: 0px;

          @include font-regular();

          &::-webkit-input-placeholder {
            color: #fed7b0;
            text-align: left;
            font-size: 16px;
            line-height: 30px;
          }
        }

        .btn-with-uid, .btn-without-uid {
          color: #3a3a3a;
          background-color: #f7cc86 !important;
          position: absolute;
          width: 215px;
          font-size: 14px;
          border-radius: 2px;

          @include font-semibold();

          &:active,
          &.active,
          &.activated {
            background: #f7cc86;
            box-shadow: none;
            opacity: 1;
          }
        }

        .btn-with-uid {
          top: 0;
          right: 30px;

          @include transition-duration($ios-transition-duration);
          @include transition-timing-function($ios-transition-timing-function);

          transition-property: width;
          -webkit-transtion-property: width;

          &.show-form {
            width: 70px;
            height: 44px;
          }
        }

        .btn-without-uid {
          bottom: 0;
        }
      }

      .text-1, .text-2, .text-3 {
        font-size: 22px;
        line-height: 30px;
        position: absolute;
        width: 210px;
        top: 175px;
        color: #fed7b0;
        text-align: center;

        @include font-regular();

        .letter-spacing {
          margin-bottom: 5px;
        }
      }

      .text-1 {
        z-index: 1;
      }

      .text-2, .hongbao-buttons {
        z-index: 2;
        opacity: 0;
        transform: translate3d(100%, 0, 0);
        -webkit-transform: translate3d(100%, 0, 0);
      }

      .text-3 {
        z-index: 3;
        opacity: 0;
        transform: translate3d(100%, 0, 0);
        -webkit-transform: translate3d(100%, 0, 0);

        .remind {
          text-align: center;
          padding-top: 52px;
          color: #FFC9A4;
          height: 12px;
          font-size: 12px;
          line-height: 12px;

          @include font-regular();
        }
      }

      .text-1, .text-2, .open-hongbao-text, .hongbao-buttons, .text-3, .btn-continue {
        @include transition-duration($ios-transition-duration);
        @include transition-timing-function($ios-transition-timing-function);

        transition-property: opacity, transform;
        -webkit-transtion-property: opacity, -webkit-transform;
      }

      .btn-open-hongbao {
        @include transition-delay(0.25s);
        @include transition-duration($ios-transition-duration);
        @include transition-timing-function($ios-transition-timing-function);

        transition-property: transform;
        -webkit-transtion-property: -webkit-transform;
      }

      .btn-continue {
        @include font-semibold();

        opacity: 0;
        transform: translate3d(100%, 0, 0);
        -webkit-transform: translate3d(100%, 0, 0);
      }

      &.step-1 {
        opacity: 1;
        transform: translate3d(0, 0, 0);
        -webkit-transform: translate3d(0, 0, 0);
      }

      &.step-2, &.step-3 {
        .text-1, .open-hongbao-text {
          opacity: 0;
          transform: translate3d(-33%, 0, 0);
          -webkit-transform: translate3d(-33%, 0, 0);
        }

        .btn-open-hongbao {
          transform: translate3d(0, 200%, 0);
          -webkit-transform: translate3d(0, 200%, 0);
        }
      }

      &.step-2 {
        .text-2, .hongbao-buttons {
          transition-delay: 0.2s;
          opacity: 1;
          transform: translate3d(0, 0, 0);
          -webkit-transform: translate3d(0, 0, 0);
        }
      }

      &.step-3 {
        .text-2, .hongbao-buttons {
          opacity: 0;
          transform: translate3d(-33%, 0, 0);
          -webkit-transform: translate3d(-33%, 0, 0);
        }

        .text-3, .continue-text {
          opacity: 1;
          transform: translate3d(0, 0, 0);
          -webkit-transform: translate3d(0, 0, 0);
        }

        .btn-continue {
          opacity: 1;
          transform: translate3d(0, 0, 0);
          -webkit-transform: translate3d(0, 0, 0);
        }
      }
    }
  }

  /* 动画 */

  @-webkit-keyframes bounceInDown {
    from, 60%, 75%, 90%, to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }

    0% {
      opacity: 0;
      -webkit-transform: translate3d(0, -3000px, 0);
      transform: translate3d(0, -3000px, 0);
    }

    60% {
      opacity: 1;
      -webkit-transform: translate3d(0, 25px, 0);
      transform: translate3d(0, 25px, 0);
    }

    75% {
      -webkit-transform: translate3d(0, -10px, 0);
      transform: translate3d(0, -10px, 0);
    }

    90% {
      -webkit-transform: translate3d(0, 5px, 0);
      transform: translate3d(0, 5px, 0);
    }

    to {
      -webkit-transform: none;
      transform: none;
    }
  }

  @keyframes bounceInDown {
    from, 60%, 75%, 90%, to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }

    0% {
      opacity: 0;
      -webkit-transform: translate3d(0, -3000px, 0);
      transform: translate3d(0, -3000px, 0);
    }

    60% {
      opacity: 1;
      -webkit-transform: translate3d(0, 25px, 0);
      transform: translate3d(0, 25px, 0);
    }

    75% {
      -webkit-transform: translate3d(0, -10px, 0);
      transform: translate3d(0, -10px, 0);
    }

    90% {
      -webkit-transform: translate3d(0, 5px, 0);
      transform: translate3d(0, 5px, 0);
    }

    to {
      -webkit-transform: none;
      transform: none;
    }
  }

  .bounceInDown {
    -webkit-animation-name: bounceInDown;
    animation-name: bounceInDown;
  }

  .popup-container[effect="hongbao"] {
    &.popup-hidden .popup {
      .popup-body > * {
        display: none;
      }

      -webkit-animation-name: slideUpOut;
      animation-name: slideUpOut;
      -webkit-animation-duration: 0.25s;
      animation-duration: 0.25s;
      -webkit-animation-timing-function: ease;
      animation-timing-function: ease;
    }

    &.active .popup {
      background: none;

      .popup-body > * {
        visibility: visible;
      }

      -webkit-animation-name: bounceInDown;
      animation-name: bounceInDown;
      -webkit-animation-duration: 0.75s;
      animation-duration: 0.75s;
    }
  }
</style>



